<template>
  <div class="my_answer">
    <div class="answer_info">
      <span>我的答案：</span>
      <span
        v-if="questionType !== 4 || !myAnswer"
        :class="myAnswer === trueAnswer ? 'answer_sucess' : ''"
      >{{myAnswer||"未作答"}}</span>
      <r-quill-editor
        v-else
        ref="REditor"
        :is-edit="false"
        :funType="modulePicture"
        :content="myAnswer"
      ></r-quill-editor>
    </div>
    <div class="answer_info" v-if="trueAnswer || getScore || getScore===0">
      <span>正确答案：</span>
      <span class="answer_true">{{trueAnswer}}</span>
    </div>
    <div class="answer_info" v-if="(trueAnswer || getScore || getScore===0) && analysis">
      <span>答案解析：</span>
      <span class="answer_analysis">{{analysis}}</span>
    </div>
    <div class="answer_info" v-if="trueAnswer || comment || getScore || getScore===0">
      <span>我的得分：</span>
      <span class="answer_score">{{getScore||0}}</span> 分
    </div>
    <div class="answer_info" v-if="comment">
      <span>老师点评：</span>
      <span class="answer_analysis">{{comment}}</span>
    </div>
  </div>
</template>
<script>
import RQuillEditor from "@/components/RQuillEditor";
export default {
  components: { RQuillEditor },
  props: {
    questionType: {
      type: Number,
      default: 1
    },
    myAnswer: {
      type: String,
      default: ""
    },
    trueAnswer: {
      // 是否已作答
      type: String,
      default: ""
    },
    getScore: {
      type: Number,
      default: null
    },
    modulePicture: {
      type: Number,
      default: 1
    },
    analysis: {
      type: String,
      default: ""
    },
    comment: {
      type: String,
      default: ""
    }
  },
  data() {
    return {};
  },
  methods: {},
  watch: {}
};
</script>

<style lang='less' scoped>
.my_answer {
  width: 92vw;
  padding: 2vh 4vw;
  text-align: left;
  background: rgba(255, 255, 255, 1);
  margin-bottom: 2vh;

  .answer_info {
    font-size: 1.4rem;
    line-height: 1.5;
    margin: 1vh 0;
    word-break: break-all;
    span:last-child {
      font-size: 1.5rem;
    }
    .answer_sucess {
      color: #07c160;
    }
    .answer_true {
      color: rgb(241, 45, 45);
    }
    .answer_analysis {
      color: #07c160;
    }
    .answer_score {
      color: #f44;
    }
  }

  /deep/ .quill-editor {
    background: #ebf0ef;
    width: 96vw;
    margin: 0 -2vw;
  }
}
</style>


