/*
** 2019-09-20
** created by gfy
** 参考demo： https://surmon-china.github.io/vue-quill-editor/
** 参考汉化： https://www.jianshu.com/p/21dab85b7fa8
** 参考自定义上传： https://github.com/surmon-china/vue-quill-editor/issues/134
*/
<template>
  <quill-editor
    v-model="contentValue"
    ref="myQuillEditor"
    :options="editorOption"
    :disabled="!isEdit"
    @blur="onEditorBlur($event)"
    @focus="onEditorFocus($event)"
    @ready="onEditorReady($event)"
    @change="updateContents($event)"
  ></quill-editor>
</template>

<script>
import VueQuillEditor from "vue-quill-editor";
import { ActionSheet } from "vant";
// import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { ModulePicture } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "RQuillEditor",
  components: {
    "quill-editor": VueQuillEditor.quillEditor,
    [ActionSheet.name]: ActionSheet
  },
  props: {
    isEdit: {
      //是否可编辑
      type: Boolean,
      default: true
    },
    funType: {
      type: Number,
      default: ModulePicture.StudentNote
    },
    content: {
      type: String,
      default: ""
    },
    maxHeight: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      // show: false,
      fileId: "",
      imgBase: "",

      contentValue: this.transformValue("show", this.content),
      defaultEditorOption: {
        placeholder: "请输入文字",
        theme: "snow", // or 'bubble'
        modules: {
          toolbar: {
            container: [
              ["bold", "italic", "underline"], // 加粗 斜体 下划线
              [{ color: [] }], // 字体颜色
              [{ header: [1, 2, 3, 4, 5, 6, false] }], // 1、2 级标题
              [{ font: [] }], // 字体种类

              ["blockquote", "code-block"], // 引用  代码块
              [{ align: [] }], // 对齐方式
              [{ indent: "-1" }, { indent: "+1" }], // 缩进
              [{ list: "ordered" }, { list: "bullet" }], // 有序、无序列表
              ["image", "photo"] // 链接、图片、视频
            ],
            handlers: {
              image: value => {
                if (value) {
                  sendToAPP(FuncType.albumOpen, "");
                  // document.querySelector("#quill-upload input").click();
                } else {
                  this.quill.format("image", false);
                }
              },
              photo: value => {
                if (value) {
                  sendToAPP(FuncType.cameraOpen, "");
                  // document.querySelector("#quill-upload input").click();
                } else {
                  this.quill.format("photo", false);
                }
              }
            }
          }
        }
      }
    };
  },
  created() {},
  computed: {
    editorOption() {
      if (this.isEdit) {
        return {
          placeholder: "请输入内容",
          theme: "snow", // or 'bubble'
          modules: {
            toolbar: {
              container: [
                ["bold"], // 加粗 斜体 下划线 , "italic", "underline"
                [{ color: [] }], // 字体颜色
                [{ header: [1, 2, 3, 4, 5, 6, false] }], // 1、2 级标题
                // [{ font: [] }], // 字体种类

                // ["blockquote", "code-block"], // 引用  代码块
                [{ align: [] }], // 对齐方式
                [{ indent: "-1" }, { indent: "+1" }], // 缩进
                // [{ list: "ordered" }, { list: "bullet" }], // 有序、无序列表
                ["image", "photo"] // 链接、图片、视频
              ],
              handlers: {
                image: value => {
                  if (value) {
                    if (
                      this.editorPictureNum() < this.$config.editorPictureMaxNum
                    ) {
                      sendToAPP(FuncType.albumOpen, "");
                    } else {
                      this.$toast("可上传图片已达到上限");
                    }
                    // document.querySelector("#quill-upload input").click();
                  } else {
                    this.quill.format("image", false);
                  }
                },
                photo: value => {
                  if (value) {
                    if (
                      this.editorPictureNum() < this.$config.editorPictureMaxNum
                    ) {
                      sendToAPP(FuncType.cameraOpen, "");
                    } else {
                      this.$toast("可上传图片已达到上限");
                    }
                    // document.querySelector("#quill-upload input").click();
                  } else {
                    this.quill.format("photo", false);
                  }
                }
              }
            }
          }
        };
      } else {
        return {
          placeholder: "没有内容",
          theme: "bubble", // or 'bubble'
          modules: {
            toolbar: {
              container: []
            }
          }
        };
      }
    }
  },

  mounted() {
    this.$nextTick(() => {
      window.uploadImage = imgFile => {
        this.afterRead(imgFile);
      };

      document.getElementsByClassName(
        "ql-editor"
      )[0].style.maxHeight = this.maxHeight;
    });
  },
  methods: {
    onEditorBlur(editor) {
      // console.log('editor blur!', editor)
    },
    onEditorFocus(editor) {
      // console.log('editor focus!', editor)
    },
    onEditorReady(editor) {
      // console.log('editor ready!', editor)
    },
    customButtonClick() {
      // alert(
      //   `You can custom the button and listen click event to do something...\n你可以定义一些自定义按钮做你想做的事，如上传图片至第三方存储再插入内容区...等等`
      // );
    },
    updateContents(e) {
      e.quill.deleteText(
        this.funType === ModulePicture.StudentNote ? 500 : 200,
        4
      ); //非笔记限制200个字数，笔记限制500个字
      this.$emit("change", this.transformValue("save", e.html));
    },

    // 图片路径转换 show转真实路径，save转存储
    transformValue(type, value) {
      let newVal = value.replace(
        /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
        (match, capture) => {
          //capture,返回每个匹配的字符串
          if (type === "show") {
            let wordArray = capture.split("/");
            if (wordArray.length === 3) {
              let filePath = this.$gatewayHttp.resourse.picture(
                wordArray[1],
                wordArray[2]
              );

              return '<img src="' + filePath + '" alt="" />';
            }
            return '<img src="' + capture + '" alt="" />';
          } else {
            let index = capture.indexOf("?");
            if (index > -1) {
              let params = capture.substring(index).split("&");
              if (params.length === 2) {
                //GetPicture?fileName=${id}&fileModule=${type}`;
                let filePath = "picture";
                params.forEach(item => {
                  let sub = item.split("=");
                  if (sub.length === 2) filePath += "/" + sub[1];
                });
                return '<img src="' + filePath + '" alt="" />';
              }
            }
            return '<img src="' + capture + '" alt="" />';
          }
        }
      );
      return newVal;
    },
    // 编辑器中的图片数量
    editorPictureNum() {
      let count = 0;
      this.contentValue.replace(
        /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
        (match, capture) => {
          count++;
        }
      );
      return count;
    },
    afterRead(file) {
      console.log("接收到的文件：", file);
      // 超过20M
      if (file.length > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      if (file) {
        let reqData = {
          funType: this.funType,
          imgBaseString: file
        };
        this.$gatewayHttp.resourse
          .upload(reqData)
          .then(res => {
            if (res.Result) {
              this.imgBase = file;
              this.fileId = res.Data;

              // res为图片服务器返回的数据
              // 获取富文本组件实例
              let quill = this.$refs.myQuillEditor.quill;
              // 如果上传成功
              if (this.fileId) {
                // 获取光标所在位置
                let length = quill.getSelection() ? quill.getSelection().index : 0;
                let filePath = this.$gatewayHttp.resourse.picture(
                  this.fileId,
                  this.funType
                );
                // 插入图片  res.info为服务器返回的图片地址
                quill.insertEmbed(length, "image", filePath);
                // 调整光标到最后
                quill.setSelection(length + 1);
              } else {
                this.$toast("图片插入失败");
              }
            }
          })
          .catch(err => {
            console.log("err:", err);
          });
      }
    },
    initValue(value) {
      this.contentValue = this.transformValue("show", value);
    }
  },
  watch: {
    maxHeight(newVal, oldVal) {
      // console.log("newVal:", newVal);
      document.getElementsByClassName("ql-editor")[0].style.maxHeight = newVal;
    }
  }
};
</script>

<style>
.editor {
  line-height: normal !important;
  height: 800px;
}
.ql-snow .ql-tooltip[data-mode="link"]::before {
  content: "请输入链接地址:";
}
.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "保存";
  padding-right: 0px;
}

.ql-snow .ql-tooltip[data-mode="video"]::before {
  content: "请输入视频地址:";
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
  content: "文本";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "标题1";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "标题2";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "标题3";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "标题4";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "标题5";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "标题6";
}

.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  content: "标准字体";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "衬线字体";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "等宽字体";
}

/*拍照样式*/
.ql-photo {
  font-family: "iconfont" !important;
  font-size: 16px;
  line-height: 18px;
  font-style: normal;
  font-weight: bolder;
  -webkit-font-smoothing: antialiased;
}
.ql-photo::before {
  content: "\E641";
}
/* 样式修改 */
.ql-toolbar.ql-snow .ql-formats {
  margin-right: 0.5rem;
}
</style>

<style lang="less" scoped>
.quill-editor,
.quill-code {
  width: 100%;
}
.quill-code {
  height: auto;
  border: none;
  > .title {
    border: 1px solid #ccc;
    border-left: none;
    height: 3em;
    line-height: 3em;
    text-indent: 1rem;
    font-weight: bold;
  }
  > code {
    width: 100%;
    margin: 0;
    padding: 1rem;
    border: 1px solid #ccc;
    border-top: none;
    border-left: none;
    border-radius: 0;
    height: 30rem;
    overflow-y: auto;
  }
}

/deep/ .ql-editor {
  min-height: 10vh;
  word-break: break-all;
  word-wrap: break-word;
}
</style>


